		const omsFormStep2Array = Object.keys(omsFormStep2).filter(key => omsFormStep2[key]);
		const omsFormStep3Array = Object.keys(omsFormStep3).filter(key => omsFormStep3[key]);
		const omsFormStep4Array = Object.keys(omsFormStep4).filter(key => omsFormStep4[key]);
		const omsFormStep5Array = Object.keys(omsFormStep5).filter(key => omsFormStep5[key]);
